/** @format */

import React, { Fragment, useEffect, useState } from "react";
import DashBoard from "../Admin/DashBoard";

import {
  Box,
  Typography,
  Tabs,
  Tab,
  IconButton,
  Select,
  MenuItem,
} from "@mui/material";
import PropTypes from "prop-types";

import SearchIcon from "@mui/icons-material/Search";
import CloseIcon from "@material-ui/icons/Close";
import { MoreVert, Close } from "@mui/icons-material";

// components of tabs
import Invoice from "../../Invoice";
import Matter from "../../Matter";
import Ledger from "../../Ledger";
import Favorite from "../../Favorite/Favorites";
import Category from "../../Category/ManageCategory";
import Contact from "../../Contact/Contact";
import Profile from "../../Profile/Profile";
import Tasks from "../../Tasks";

const data = [
  { name: "invoice", content: <Invoice /> },
  { name: "matter", content: <Matter /> },
  { name: "ledger", content: <Ledger /> },
  { name: "favorite", content: <Favorite /> },
  { name: "category", content: <Category /> },
  { name: "contact", content: <Contact /> },
  { name: "profile", content: <Profile /> },
  { name: "tasks", content: <Tasks /> },
];

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography component="span">{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

const AdminDashboard = () => {
  const [wordEntered, setWordEntered] = useState("");
  const searchItem = () => {
    let selectedItem = data.filter((value) => {
      if (value.name === wordEntered) {
        return true;
      }
    });
  };

  const handleChangeSearch = (e) => {
    setWordEntered(e.target.value);
    console.log(" we are in handle change search");
    searchItem();
  };
  const searchTab = () => {
    return (
      <Box>
        <Box
          noValidate
          sx={{
            p: "2px 4px",
            display: "flex",
            alignItems: "center",
            width: "100%",
            borderRadius: "5rem",
            backgroundColor: "#dfdfdf",
          }}
        >
          <Select
            fullWidth
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={wordEntered}
            label="Search...."
            onChange={handleChangeSearch}
          >
            {data.map((item, index) => (
              <MenuItem key={index} value={item.name}>
                {item.name}
              </MenuItem>
            ))}
          </Select>

          <IconButton type="submit" sx={{ p: "10px" }} aria-label="search">
            {wordEntered.length === 0 ? <SearchIcon /> : <CloseIcon />}
          </IconButton>
        </Box>
      </Box>
    );
  };
  let tabs = [
    {
      id: 0,
      name: "Dashboard",
      content: <DashBoard />,
    },
    {
      id: 1,
      name: "+",
      content: searchTab(),
    },
  ];
  const [value, setValue] = React.useState(0);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  const renderTabs = () => {
    return (
      <Box sx={{ width: "100%" }}>
        <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
          <Tabs
            value={value}
            onChange={handleChange}
            aria-label="basic tabs example"
          >
            {tabs.map((tab) =>
              tab.id === 0 || tab.id === 1 ? (
                <Tab
                  icon={<MoreVert />}
                  iconPosition="start"
                  label={tab.name}
                  {...a11yProps(tab.id)}
                />
              ) : (
                <Tab
                  icon={<Close />}
                  iconPosition="end"
                  label={tab.name}
                  {...a11yProps(tab.id)}
                />
              ),
            )}
          </Tabs>
        </Box>

        {tabs.map((tab) => (
          <TabPanel value={value} index={tab.id}>
            {tab.content}
          </TabPanel>
        ))}
      </Box>
    );
  };
  useEffect(() => {
    renderTabs();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return <Fragment>{renderTabs()}</Fragment>;
};

export default AdminDashboard;
